
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

export default {
components: {
ViewBox,
XHeader,
Group,
CheckIcon,
Masker,
Cell
},
data () {
return {
showHeadImg: false
}
},
mounted () {
this.showUserInfo()
},
activated () {
},
computed: {
phoneNum () {
return this.$store.state.userInfo.cellphone
},
isLogin () {
return this.$store.state.userInfo.token
},
isLoginAndVerfied () {
return this.$store.state.userInfo.token !== '' && this.$store.state.userInfo.isAuth
},
hasHeadImg () {
return this.$store.state.userInfo.avatar !== ''
},
headImgSrc () {

return this.$store.state.baseUrl + '/app/fileUpload/showHeadImage?fileName=' + this.$store.state.userInfo.avatar
}
},
methods: {
login () {
this.$router.push('/login')
},
showUserInfo () {
let self = this
let token = self.$store.state.userInfo.token

let path = self.$store.state.baseUrl + '/app/xsyd/loginPageInit.do'
let data = {
action: 'init_request',
path: path,
params: {
userToken: token
}
}
if (token !== '' && token !== undefined) {
self.$store.dispatch('normalRequest', data).then(res => {
res.response === 'success' ? self.$store.commit('INIT_HEAD_IMG', res.data) : self.$vux.toast.text('头像信息获取失败')
})
} else {
self.$vux.toast.text('请先登录')
}
},
uploadHeadImg () {
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
return
}
this.$router.push('/myData')
},
hideHeadImg () {
this.showHeadImg = false
},
shareCodePic () {

let self = this
let data = {
action: 'jump_qr_show'
}
self.$store.dispatch('normalRequest', data).then(res => {
})
},
toGuarantee () {
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
return
}
if (this.$store.state.userInfo.isAuth === true) {
this.$router.push('/myGurantee')
} else {
this.toVerfiedPage()
}
},
toBankCardList () {
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
return
}
if (this.$store.state.userInfo.isAuth === true) {
this.$router.push('/bankCardList')
} else {
this.toVerfiedPage()
}
},
toCouponList () {
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
return
}
if (this.$store.state.userInfo.isAuth === true) {
this.$router.push('/couponList')
} else {
this.toVerfiedPage()
}
},
toSetting () {
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
return
}
this.$router.push('/setting')
}
}
}



